@import "./common.less";
@color:blue;
@baseColor:yellowgreen;

html,body,.pyg_view{
    height:  100%;
}
body{
    padding-top: 45px;
    padding-bottom: 45px;
}
.pyg_view{
    overflow: scroll;
    background-color: aqua;
}

.pyg_header{
    // background-color: #f00;
    padding: 5px;
    width: 100%;
    height: 45px;
    position: fixed;
    background-color: #fff;
    left: 0;
    top: 0;
}
.pyg_footer{
    background-color: #00f;
    width: 100%;
    height: 45px;
    position: fixed;
    left: 0;
    bottom: 0;
}